let sourceType = [
	['camera'],
	['album'],
	['camera', 'album']
]
let sizeType = [
	['compressed'],
	['original'],
	['compressed', 'original']
]
import showComfirm from '@/components/showComfirm/showComfirm.vue';
import showModal from '@/components/showModal/showModal.vue'
export default {
	components:{
		showComfirm,
		showModal
	},
	data() {
		return {
			title: 'choose/previewImage',
			imageList: [],
			sourceTypeIndex: 2,
			sourceType: ['拍照', '相册', '拍照或相册'],
			sizeTypeIndex: 2,
			sizeType: ['压缩', '原图', '压缩或原图'],
			countIndex: 8,
			count: [1, 2, 3, 4, 5, 6, 7, 8, 9]	
		};
	},
	watch:{
		imageList(e){
			this.$emit('getSelectImage',{e});
		}
	},
	methods:{
		comfirmDelete(e){
		    let {status} = e;
			if(status){
			    this.imageList.splice(this.deleteIndex,1);
			}else{
				return;
			}
		},
		deleteSomneOne(index){
			this.deleteIndex = index;
			this.$refs.comfirm.showComfirm('确认删除该图片？');
		},
		async chooseImage() {









		
			if (this.imageList.length === 9) {
				this.$refs.notice.showMsg({msg:'最多上传9张图片',status:'error'});
				return
			}
			uni.chooseImage({
				sourceType: sourceType[this.sourceTypeIndex],
				sizeType: sizeType[this.sizeTypeIndex],
				count: this.imageList.length + this.count[this.countIndex] > 9 ? 9 - this.imageList.length : this.count[this.countIndex],
				success: (res) => {
					this.imageList = this.imageList.concat(res.tempFilePaths);
				},
				fail: (err) => {






					uni.getSetting({
						success: (res) => {
							let authStatus = false;
							switch (this.sourceTypeIndex) {
								case 0:
									authStatus = res.authSetting['scope.camera'];
									break;
								case 1:
									authStatus = res.authSetting['scope.album'];
									break;
								case 2:
									authStatus = res.authSetting['scope.album'] && res.authSetting['scope.camera'];
									break;
								default:
									break;
							}
							if (!authStatus) {
								uni.showModal({
									title: '授权失败',
									content: 'Hello uni-app需要从您的相机或相册获取图片，请在设置界面打开相关权限',
									success: (res) => {
										if (res.confirm) {
											uni.openSetting()
										}
									}
								})
							}
						}
					})

				}
			})
		},
		previewImage(e){
			var current = e.target.dataset.src
			uni.previewImage({
				current: current,
				urls: this.imageList
			})
		},
	}
}
